import React, { Component } from 'react';
import './App.css';
import Question from "./components/question/question";
import Welcome from "./components/welcome/welcome";
import MyGraph from "./components/graph/graph";
import TestCoverage from "./components/testCoverage/testCoverage";



class App extends Component {
  constructor(props){
    super(props);
    this.state = {
      activeComponent: "welcome"
    }
  }

  showQuestion = () => {
    this.setState({activeComponent:"question"})
  };

  showGraph = () => {
      this.setState({activeComponent:"graph"})
  };

  showTest = () => {
        this.setState({activeComponent:"test"})
  };


  render() {
     let myComponent = <Welcome/>;
    if (this.state.activeComponent === "question"){
      myComponent = <Question/>;
    } else if (this.state.activeComponent === "graph") {
        myComponent = <MyGraph/>;
    } else if (this.state.activeComponent === "test") {
        myComponent = <TestCoverage/>;
    }
    return (

      <div className="App-header">
          <link
              rel="stylesheet"
              href="https://maxcdn.bootstrapcdn.com/bootstrap/4.3.1/css/bootstrap.min.css"
              integrity="sha384-ggOyR0iXCbMQv3Xipma34MD+dH/1fQ784/j6cY/iJTQUOhcWr7x9JvoRxT2MZw1T"
              crossorigin="anonymous"
          />
          <div className="left-bar">
          <button className="barButton" onClick={this.showQuestion}>Question</button>
          <button className="barButton" onClick={this.showGraph}>Sample Graph</button>
          <button className="barButton" onClick={this.showTest}>Sample Test Cases</button>
        </div>
        <div className="right-pane">
            {myComponent}
        </div>

      </div>
    );
  }
}

export default App;
